  @import "~@/styles/variables.scss";

  .nav-logo {
    float: left;
  }

  .el-menu {
    float: left;
  }

  .el-dropdown {
    margin-right: 30px;
    float: right;

    &:focus {
      outline: none;
    }

    .el-dropdown-link {
      line-height: $NavMenuHeight;

      .avatar {
        border-radius: 10px;
        width: 40px;
        height: 40px;
        vertical-align: middle;
      }
    }

    .user-name {
      font-size: 18px;
      height: 100%;
      color: #f3f4f7;
      float: right;
    }

    .item {
      display: inline-block;
      padding: 0 8px;
      height: 100%;
      font-size: 18px;
      color: #f3f4f7;
      vertical-align: text-bottom;

      &.hover-effect {
        cursor: pointer;
        transition: background .3s;

        &:hover {
          background: rgba(0, 0, 0, .025)
        }
      }
    }
  }

</style>
